body {
    background: url(../src/img/background_1920.jpg) no-repeat;
    background-color: #000000;
    background-size: 100%;
}

.wrapper {
    margin: auto;
    padding-left: 20px;   
    padding-right: 20px;
    max-width: 1100px;
    background: #000000a1;
    min-height: 100vh;
}

@media screen and (max-width: 1200px) {
    .wrapper {
        max-width: 900px;
    }
}

@media screen and (max-width: 880px) {
    .wrapper {
        max-width: 660px;
    }
}

@media screen and (max-width: 660px) {
    .wrapper {
        max-width: 500px;
    }
}

@media screen and (max-width: 500px) {
    .wrapper {
        max-width: 330px;
    }
}



